
@import "fonts/ginto-nord/stylesheet.css";
@import url('https://fonts.googleapis.com/css2?family=Anton&display=swap');


.App {
  text-align: center;
}

.App-logo {
  transform: scale(0.6);
}

.logo {
  font-family: "Anton", sans-serif;
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    scale: 2;
  }
}

.App-header {
  background-color: #7d3200;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.dv {
  border-radius: 20px;
  padding: 10px;
  background-color: rgb(74, 74, 74);
  color: #c2c2c2;
}

.lop {
  border-radius: 20px;
  padding: 10px;
  background-color: rgb(50, 0, 151);
  color: #000000;
}

.sylo {
  border-radius: 20px;
  padding: 10px;
  background-color: rgb(161, 0, 0);
  color: #000000;
}

.email {
  border-radius: 20px;
  padding: 10px;
  background-color: rgb(0, 0, 0);
  color: #c2c2c2;
}

.discord {
  font-family: "Ginto Nord";
  border-radius: 20px;
  padding: 10px;
  background-color: #5865F2;
  color: #23272A ;
}


.madewith {
  /* put it at the bottom right of the screen */
  position: fixed;
  padding: 20px;
  bottom: 0;
  left: 0;
  /* make it as small as possible */
  width: auto;
  height: auto;
}

.reactlogo {
  /* put it at the bottom right of the screen, next to the made with class */
  position: fixed;
  padding: 40px;
  bottom: 0;
  left: 0;
  transform: translate(75%, 8%);
  /* make it as small as possible */
  width: 100px;
  height: 100px;
}

